<template>
  <div class="rpl-matriks">
    <h1 class="content-header">{{ this.$route.name }}</h1>
    <div v-if="$route.params.render == 'daftar'" class="content">
      <base-header class="pb-6" type="primary">
        <div class="row">
          <div class="col-lg-12 py-2 col-5 text-right">
            <el-select
              v-show="$store.getters.isAdmin"
              v-model="selects.unitId"
              size="mini"
              placeholder="Unit Kerja"
              class="mr-2"
              @change="getData(selects.unitId)"
            >
              <!-- <el-option label="All" value="9999"></el-option> -->
              <el-option
                v-for="option in cabang"
                :key="option.label"
                :label="option.label"
                :value="option.value"
              ></el-option>
            </el-select>
            <!-- <el-select
              v-model="periode"
              size="mini"
              placeholder="Pilih Periode"
              class="mr-2"
              @change="changePeriode(periode)"
            >
              <el-option
                v-for="option in listPeriode"
                :key="option.label"
                :label="option.label"
                :value="option.value"
              ></el-option>
            </el-select> -->
            <base-button size="sm" type="neutral" @click="showForm"
              >Tambah</base-button
            >
          </div>
        </div>
      </base-header>
      <div class="container-fluid mt--6">
        <div>
          <div class="card">
            <div class="card-header">
              <h2 class="m-0">Daftar Matriks Rencana Pemantauan Lingkungan</h2>
            </div>
            <div>
              <el-table
                :data="queriedData"
                align="center"
                row-key="id"
                header-row-class-name="thead-light"
                @sort-change="sortChange"
                @row-click="toDetail"
              >
                <el-table-column
                  sortable
                  v-for="column in tableColumns"
                  :key="column.label"
                  v-bind="column"
                ></el-table-column>
                <!-- <el-table-column label="Tolak Ukur" min-width="150" align="left">
                  <template slot-scope="tolUkur">
                    <span v-html="tolUkur.row.MRKL_TOLUKUR"></span>
                  </template>
                </el-table-column>-->
              </el-table>
            </div>
            <div
              slot="footer"
              class="col-12 px-4 py-2 d-flex justify-content-center justify-content-sm-between flex-wrap"
            >
              <div class="d-flex align-items-center">
                <p class="card-category mb-0 mr-2">Menampilkan</p>
                <div style="width: 80px">
                  <el-select
                    class="select-primary pagination-select"
                    v-model="pagination.perPage"
                    placeholder="Per page"
                    size="mini"
                  >
                    <el-option
                      class="select-primary"
                      v-for="item in pagination.perPageOptions"
                      :key="item"
                      :label="item"
                      :value="item"
                    ></el-option>
                  </el-select>
                </div>
                <p class="card-category m-0 ml-2">
                  Tampil {{ from + 1 }} sampai {{ to }} dari {{ total }} data
                  <span v-if="selectedRows.length"
                    >&nbsp; &nbsp; {{ selectedRows.length }} rows selected</span
                  >
                </p>
              </div>
              <base-pagination
                class="pagination-no-border my-3"
                v-model="pagination.currentPage"
                :per-page="pagination.perPage"
                :total="total"
              ></base-pagination>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div v-if="$route.params.render == 'form'" class="content">
      <base-header class="pt-4 pb-6" type="primary"></base-header>
      <div class="container-fluid mt--6">
        <div class="card border-0">
          <div class="card-header">
            <h2>Form Matriks Rencana Pemantauan Lingkungan</h2>
          </div>
          <div class="card-body">
            <base-input
              v-show="$store.getters.isAdmin"
              v-if="!isUpdate"
              label="Unit Kerja"
              placeholder="Pilih Unit Kerja"
              class="form-group"
            >
              <select
                v-model="uk_id"
                @change="checkILID(uk_id)"
                class="form-control"
              >
                <option
                  v-for="option in dataShift(cabang)"
                  :key="option.label"
                  :label="option.label"
                  :value="option.value"
                ></option>
              </select>
            </base-input>
            <base-input label="Periode" class="form-group">
              <select
                v-model="formMatriksRPL.mrpl_periode"
                class="form-control"
              >
                <option
                  v-for="option in listPeriode"
                  :key="option.label"
                  :label="option.label"
                  :value="option.value"
                ></option>
              </select>
            </base-input>
            <base-input label="Izin Lingkungan" class="form-group">
              <select v-model="formMatriksRPL.il_id" class="form-control">
                <option
                  v-for="option in listIL"
                  :key="option.label"
                  :label="option.label"
                  :value="option.value"
                ></option>
              </select>
            </base-input>
            <base-input label="Tahapan" class="form-group">
              <select
                v-model="formMatriksRPL.mrpl_tahapan"
                class="form-control"
              >
                <option>Pra-Konstruksi</option>
                <option>Konstruksi</option>
                <option>Operasi</option>
                <option>Paska Oprasi</option>
              </select>
            </base-input>
            <base-input label="Kegiatan" class="form-group">
              <textarea
                v-model="formMatriksRPL.mrpl_kegiatan"
                class="form-control"
                rows="1"
                resize="none"
              ></textarea>
            </base-input>
            <base-input label="Sumber Dampak" class="form-group">
              <textarea
                v-model="formMatriksRPL.mrpl_sumberdampak"
                class="form-control"
                rows="1"
                resize="none"
              ></textarea>
            </base-input>
            <base-input label="Jenis Limbah/Cemaran" class="form-group">
              <textarea
                v-model="formMatriksRPL.mrpl_jenlimbah"
                class="form-control"
                rows="1"
                resize="none"
              ></textarea>
            </base-input>

            <h5>Besaran Dampak</h5>
            <form>
              <html-editor
                v-model="formMatriksRPL.mrpl_besdampak"
              ></html-editor>
            </form>
            <h5>Tolak Ukur Dampak</h5>
            <form>
              <html-editor v-model="formMatriksRPL.mrpl_tolukur"></html-editor>
            </form>
            <h5>Tujuan</h5>
            <form>
              <html-editor v-model="formMatriksRPL.mrpl_tujuan"></html-editor>
            </form>
            <div class="row mb-2">
              <div class="col-6">
                <div class="row align-items-center">
                  <div class="col">
                    <h5 class="m-0">Upaya Pengelolaan Lingkungan</h5>
                  </div>
                </div>
              </div>
              <div class="col-6">
                <div class="row align-items-center">
                  <div class="col">
                    <h5 class="m-0 text-right">Jumlah Upaya</h5>
                  </div>
                  <div class="col-2 mb--4">
                    <base-input v-model="jumlahUpaya" />
                  </div>
                </div>
              </div>
            </div>
            <form>
              <html-editor
                v-model="formMatriksRPL.mrpl_upengelolaan[index - 1]"
                v-for="index in nullHandler(jumlahUpaya)"
                :key="index"
              ></html-editor>
            </form>
            <div class="row mb-2">
              <div class="col-6">
                <div class="row align-items-center">
                  <div class="col">
                    <h5 class="m-0">Parameter Pemantauan</h5>
                  </div>
                </div>
              </div>
              <div class="col-6">
                <div class="row align-items-center">
                  <div class="col">
                    <h5 class="m-0 text-right">Jumlah Parameter</h5>
                  </div>
                  <div class="col-2 mb--4">
                    <base-input v-model="jumlahParameter" />
                  </div>
                </div>
              </div>
            </div>

            <div v-for="index in nullHandler(jumlahParameter)" :key="index">
              <div class="row" v-if="jumlahParameter > 0">
                <div class="col-4">
                  <base-input
                    v-model="tempParam[index - 1][0]"
                    label="Nama"
                  ></base-input>
                </div>
                <div class="col-4">
                  <base-input
                    type="number"
                    v-model="tempParam[index - 1][1]"
                    label="Baku Mutu"
                  ></base-input>
                </div>
                <div class="col-4">
                  <base-input
                    v-model="tempParam[index - 1][2]"
                    label="Satuan"
                  ></base-input>
                </div>
              </div>
            </div>

            <h5>Lokasi</h5>
            <form>
              <html-editor v-model="formMatriksRPL.mrpl_loc"></html-editor>
            </form>
            <h5>Periode</h5>
            <form>
              <html-editor v-model="formMatriksRPL.mrpl_periode"></html-editor>
            </form>
            <h5>Pelaksana</h5>
            <form>
              <html-editor
                v-model="formMatriksRPL.mrpl_pelaksana"
              ></html-editor>
            </form>
            <h5>Pengawas</h5>
            <form>
              <html-editor v-model="formMatriksRPL.mrpl_pengawas"></html-editor>
            </form>
            <h5>Pelaporan</h5>
            <form>
              <html-editor
                v-model="formMatriksRPL.mrpl_pelaporan"
              ></html-editor>
            </form>

            <div class="col-lg-12 col-5 px-0 pt-4 text-right">
              <base-button size="md" type="danger" @click="hideForm"
                >Kembali</base-button
              >
              <base-button
                v-if="!isUpdate"
                size="md"
                type="primary"
                @click="submitForm"
                >Simpan</base-button
              >
              <base-button
                v-if="isUpdate"
                size="md"
                type="primary"
                @click="submitFormUpdate"
                >Update</base-button
              >
            </div>
          </div>
        </div>
      </div>
    </div>
    <div v-if="$route.params.render == 'detail'">
      <base-header class="pb-6" type="primary">
        <div class="row">
          <div class="col-lg-12 py-2 col-5 text-right">
            <base-button
              @click="toUpdateForm"
              class="ml-2"
              size="sm"
              type="neutral"
              >Ubah</base-button
            >
            <base-button
              size="sm"
              type="danger"
              @click="deleteData(tempEdit.MATRIKSRPL_ID)"
              >Hapus</base-button
            >
          </div>
        </div>
      </base-header>
      <div class="card border-0 m-4">
        <div class="card-header mt--6">
          <h2>{{ detailMatriks.title }}</h2>
        </div>
        <div class="card-body">
          <div class="col-lg-12 py-2 col-5 text-left">
            <div class="row" v-for="body in detailMatriks.body">
              <h3 class="col-2">{{ body.judul }}</h3>
              <p v-if="isText(body.type)" class="col-10">{{ body.isi }}</p>
              <span
                v-if="isHTML(body.type)"
                class="col-10"
                v-html="body.isi"
              ></span>
              <div v-if="isMany(body.type)" class="col-10">
                <span
                  class="col-12"
                  v-for="isian in body.isi"
                  v-html="isian"
                ></span>
              </div>

              <span v-if="isParam(body.type)" v-html="body.isi"></span>
              <hr class="w-100 mb-3" />
            </div>
            <!-- <base-button size="sm" type="neutral" @click="hideForm">Cancel</base-button>
            <base-button size="sm" type="neutral">Simpan</base-button>-->
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import "sweetalert2/dist/sweetalert2.css";
import HtmlEditor from "@/components/Inputs/HtmlEditor";
import DropzoneFileUpload from "@/components/Inputs/DropzoneFileUpload";
import flatPicker from "vue-flatpickr-component";
import "flatpickr/dist/flatpickr.css";
import { Table, TableColumn, Select, Option } from "element-ui";
import { BasePagination, BaseInput } from "@/components";
import clientPaginationMixin from "@/components/clientPaginationMixin";
import swal from "sweetalert2";
import users from "./users2";
import axios from "axios";
import cabang from "../../cabang";
import listPeriode from "@/views/Pages/listPeriode.js";
import listIL from "@/views/Pages/izinLingkunganList.js";

export default {
  listPeriode,
  mixins: [clientPaginationMixin],
  components: {
    HtmlEditor,
    BasePagination,
    BaseInput,
    flatPicker,
    DropzoneFileUpload,
    [Select.name]: Select,
    [Option.name]: Option,
    [Table.name]: Table,
    [TableColumn.name]: TableColumn
  },
  data() {
    return {
      listIL,
      periode: "",
      listPeriode,
      isUpdate: false,
      cabang,
      selects: {
        unitId: null
      },
      tempParam: [["", "", ""]],
      jumlahParameter: 1,
      jumlahUpaya: 1,
      uk_id: null,
      formMatriksRPL: {
        periode: "",
        il_id: 0,
        mrpl_tahapan: "",
        mrpl_kegiatan: "",
        mrpl_sumberdampak: "",
        mrpl_jenlimbah: "",
        mrpl_besdampak: "",
        mrpl_tolukur: "",
        mrpl_tujuan: "",
        mrpl_upengelolaan: [],
        mrpl_parampemantauan: "",
        mrpl_loc: "",
        mrpl_periode: "",
        mrpl_pelaksana: "",
        mrpl_pengawas: "",
        mrpl_pelaporan: ""
      },
      detailMatriks: {},
      dates: {
        simple: new Date("2020-10-10"),
        range: "2019-04-19 to 2019-04-24"
      },
      isTable: true,
      isForm: false,
      isDetail: false,
      propsToSearch: ["name", "email", "age"],
      tableColumns: [
        {
          prop: "UK_NAME",
          label: "Unit Kerja",
          minWidth: 150
        },
        {
          prop: "IL_NAME",
          label: "Izin Lingkungan",
          minWidth: 150
        },
        {
          prop: "MRPL_KEGIATAN",
          label: "KEGIATAN",
          minWidth: 150
        },
        {
          prop: "MRPL_SUMBERDAMPAK",
          label: "Sumber Dampak",
          minWidth: 150
        }
      ],
      tableData: [],
      selectedRows: [],
      tempEdit: {},
      matriksrpl_id: null
    };
  },
  watch: {
    jumlahParameter() {
      this.tempParam = [];
      if (this.isUpdate) {
        this.tempParam = JSON.parse(this.tempEdit.MRPL_PARAMPEMANTAUAN);
      }
      let gap = this.jumlahParameter - this.tempParam.length;
      for (let i = 0; i < gap; i++) {
        console.log("masuk");
        this.tempParam.push(["", "", ""]);
      }
      console.log(this.tempParam);
    }
  },
  created() {
    this.insertUKID();
    // this.getListPeriode();
  },
  computed: {},
  methods: {
    dataShift(data) {
      let tempData = [];
      for (let i = 1; i < data.length; i++) {
        tempData.push(data[i]);
      }
      return tempData;
    },
    changePeriode(value) {},
    getListPeriode() {
      axios
        .get("http://45.127.133.75:3000/api/iems/list-periode")
        .then(res => {
          console.log(res.data);
          let tempData = [];
          res.data.forEach(item => {
            tempData.push({
              name: item.PERIODE,
              value: item.PERIODE
            });
          });
          console.log(tempData);
          this.listPeriode = tempData;
        })
        .catch(err => {
          console.log(err);
        });
    },
    deleteData(id) {
      swal
        .fire({
          title: "Apakah anda yakin untuk menghapus?",
          showCancelButton: true,
          confirmButtonText: `Hapus`,
          cancelButtonText: `Kembali`,
          confirmButtonColor: "#d33",
          cancelButtonColor: "#3085d6",
          icon: "question"
        })
        .then(result => {
          /* Read more about isConfirmed, isDenied below */
          if (result.isConfirmed) {
            axios
              .delete("http://45.127.133.75:3000/api/iems/mrpl/" + id)
              .then(res => {
                swal
                  .fire({
                    title: `Success`,
                    text: "Berhasil terhapus!",
                    buttonsStyling: false,
                    confirmButtonClass: "btn btn-success",
                    icon: "success"
                  })
                  .then(result => {
                    this.insertUKID();
                    this.$router.push("daftar");
                  });
              })
              .catch(err => {
                console.log(err);
                this.swalError("Terjadi Kesalahan " + err);
              });
          }
        });
    },
    insertUKID() {
      this.getData(this.$store.getters.getUKId);
      if (!this.$store.getters.isAdmin) {
        this.uk_id = this.$store.getters.getUKId;
        this.getILID(this.$store.getters.getUKId);
      }
    },
    swalWarning(message) {
      swal.fire({
        title: `Warning`,
        text: message,
        buttonsStyling: false,
        confirmButtonClass: "btn btn-warning",
        icon: "warning"
      });
    },
    toUpdateForm() {
      this.$router.push("form");
      this.isUpdate = true;
      this.getILID(this.tempEdit.UNITKERJA_ID);
      // this.matriksrpl_id = this.tempEdit.MATRIKSRKL_ID;
      this.formMatriksRPL.il_id = this.tempEdit.IL_ID;
      this.formMatriksRPL.mrpl_besdampak = this.tempEdit.MRPL_BESDAMPAK;
      this.formMatriksRPL.mrpl_jenlimbah = this.tempEdit.MRPL_JENLIMBAH;
      this.formMatriksRPL.mrpl_kegiatan = this.tempEdit.MRPL_KEGIATAN;
      this.formMatriksRPL.mrpl_loc = this.tempEdit.MRPL_LOC;
      this.formMatriksRPL.mrpl_pelaksana = this.tempEdit.MRPL_PELAKSANA;
      this.formMatriksRPL.mrpl_pelaporan = this.tempEdit.MRPL_PELAPORAN;
      this.formMatriksRPL.mrpl_tolukur = this.tempEdit.MRPL_TOLUKUR;
      this.formMatriksRPL.mrpl_tujuan = this.tempEdit.MRPL_TUJUAN;
      this.formMatriksRPL.mrpl_tahapan = this.tempEdit.MRPL_TAHAPAN;

      this.formMatriksRPL.mrpl_sumberdampak = this.tempEdit.MRPL_SUMBERDAMPAK;
      // this.formMatriksRPL.mrpl_upengelolaan=this.tempEdit.MRPL_UPENGELOLAAN
      // this.formMatriksRPL.mrpl_parampemantauan = this.tempEdit.MRPL_PARAMPEMANTAUAN;
      this.formMatriksRPL.mrpl_periode = this.tempEdit.MRPL_PERIODE;
      this.formMatriksRPL.mrpl_pengawas = this.tempEdit.MRPL_PENGAWAS;
      let tempUpdateUP = JSON.parse(this.tempEdit.MRPL_UPENGELOLAAN);
      this.jumlahUpaya = tempUpdateUP.length;
      // for (let i = 0; i < this.jumlahUpaya; i++) {
      //   this.formMatriksRPL.mrpl_upengelolaan[i] = tempUpdateUP[i];
      // }
      this.formMatriksRPL.mrpl_upengelolaan = tempUpdateUP;
      let tempUpParam = JSON.parse(this.tempEdit.MRPL_PARAMPEMANTAUAN);
      this.jumlahParameter = tempUpParam.length;
      this.tempParam = tempUpParam;
    },
    isParam(data) {
      return data === "param";
    },
    isHTML(data) {
      return data === "HTML";
    },
    isText(data) {
      return data === "text";
    },
    isMany(data) {
      return data === "many";
    },
    isNotMany(data) {
      return data !== "many";
    },
    toDetail(row, col, event) {
      this.tempEdit = row;
      this.matriksrpl_id = row.MATRIKSRPL_ID;
      let tempUpengelolaan = JSON.parse(row.MRPL_UPENGELOLAAN);

      for (let i = 0; i < tempUpengelolaan.length; i++) {
        tempUpengelolaan[i] += "<hr />";
      }
      let tempParamIsi = JSON.parse(row.MRPL_PARAMPEMANTAUAN);

      let tempParamString = "";
      for (let i = 0; i < tempParamIsi.length; i++) {
        const coba = `<p>Nama: ${tempParamIsi[i][0]} | Baku Mutu: ${tempParamIsi[i][1]} | Satuan: ${tempParamIsi[i][2]}`;
        tempParamString += coba;
      }

      let dataDetail = {
        title: row.MRPL_KEGIATAN,
        body: [
          { judul: "Tahapan", isi: row.MRPL_TAHAPAN, type: "text" },
          {
            judul: "Kegiatan",
            isi: row.MRPL_KEGIATAN,
            type: "text"
          },
          {
            judul: "Sumber Dampak",
            isi: row.MRPL_SUMBERDAMPAK,
            type: "text"
          },
          {
            judul: "Jenis Limbah/Cemaran",
            isi: row.MRPL_JENLIMBAH,
            type: "text"
          },
          { judul: "Besaran Dampak", isi: row.MRPL_BESDAMPAK, type: "HTML" },
          { judul: "Tolak Ukur Dampak", isi: row.MRPL_TOLUKUR, type: "HTML" },
          {
            judul: "Upaya Pengelolaan Lingkungan",
            isi: tempUpengelolaan,
            type: "many"
          },
          {
            judul: "Parameter Pemantauan",
            isi: tempParamString,
            type: "param"
          },
          {
            judul: "Lokasi",
            isi: row.MRPL_LOC,
            type: "HTML"
          },
          {
            judul: "Periode Kegiatan",
            isi: row.MRPL_PERIODE,
            type: "HTML"
          },
          {
            judul: "Pelaksana",
            isi: row.MRPL_PELAKSANA,
            type: "HTML"
          },
          {
            judul: "Pengawas",
            isi: row.MRPL_PENGAWAS,
            type: "HTML"
          },
          {
            judul: "Pelaporan",
            isi: row.MRPL_PELAPORAN,
            type: "HTML"
          }
        ]
      };
      this.detailMatriks = dataDetail;
      this.$router.push("detail");
    },
    checkILID(UKID) {
      function compare(a, b) {
        // Use toUpperCase() to ignore character casing
        const nameA = a.label.toUpperCase();
        const nameB = b.label.toUpperCase();

        let comparison = 0;
        if (nameA > nameB) {
          comparison = 1;
        } else if (nameA < nameB) {
          comparison = -1;
        }
        return comparison;
      }
      let array = [];
      axios.get("http://45.127.133.75:3000/api/iems/il/" + UKID).then(res => {
        const tempData = res.data;
        tempData.forEach(item => {
          array.push({
            label: item.IL_NAME,
            value: item.IL_ID
          });
        });
        array.sort(compare);
        if (res.data) {
          this.listIL = array;
        } else {
          swal
            .fire({
              title: `Unit Kerja ini belum memiliki Izin Lingungan`,
              text: "Silakan isi Izin Lingungan terlebih dahulu!",
              buttonsStyling: false,
              showCancelButton: true,
              confirmButtonText: "Ke Form Izin Lingkungan",
              cancelButtonText: "Kembali",
              confirmButtonClass: "btn btn-success",
              cancelButtonClass: "btn btn-danger",
              icon: "error"
            })
            .then(result => {
              /* Read more about isConfirmed, isDenied below */
              if (result.isConfirmed) {
                this.$router.push("/iems/izin-lingkungan/form");
              }
            });
        }
      });
    },
    getILID(data) {
      axios
        .get("http://45.127.133.75:3000/api/iems/il-latest/" + data)
        .then(res => {
          this.formMatriksRPL.il_id = res.data[0].IL_ID;
          console.log(this.formMatriksRPL.il_id);
        });
    },
    getData(ukid) {
      console.log(ukid);
      console.log("kur");
      axios
        .get("http://45.127.133.75:3000/api/iems/rpl/" + ukid)
        .then(res => {
          console.log(res.data);
          let tempTable = res.data;
          this.tableData = tempTable;
        })
        .catch(err => {
          console.log(err);
        });
    },
    submitForm() {
      let formData = this.formMatriksRPL;
      console.log(formData.il_id);
      if (formData.il_id == 0) {
        swal.fire({
          title: `Warning`,
          text: "Pilih Unit Kerja terlebih dahulu!!!",
          buttonsStyling: false,
          confirmButtonClass: "btn btn-warning",
          icon: "warning"
        });
        return;
      }

      formData.mrpl_upengelolaan = JSON.stringify(
        this.formMatriksRPL.mrpl_upengelolaan
      );
      formData.mrpl_parampemantauan = JSON.stringify(this.tempParam);
      axios
        .post("http://45.127.133.75:3000/api/iems/rpl-new", formData)
        .then(res => {
          swal
            .fire({
              title: `Success`,
              text: "Form berhasil tersimpan!",
              buttonsStyling: false,
              confirmButtonClass: "btn btn-success",
              icon: "success"
            })
            .then(result => {
              this.insertUKID();
              this.$router.push("daftar");
            });
        })
        .catch(err => {
          console.log(err);
          swal.fire({
            title: `Warning`,
            text: "Form tidak tersimpan!!!",
            buttonsStyling: false,
            confirmButtonClass: "btn btn-warning",
            icon: "warning"
          });
        });
    },
    submitFormUpdate() {
      let formData = this.formMatriksRPL;
      if (!formData.il_id) {
        swal.fire({
          title: `Warning`,
          text: "Pilih Unit Kerja terlebih dahulu!!!",
          buttonsStyling: false,
          confirmButtonClass: "btn btn-warning",
          icon: "warning"
        });
        return;
      }

      formData.mrpl_upengelolaan = JSON.stringify(
        this.formMatriksRPL.mrpl_upengelolaan
      );

      formData.mrpl_parampemantauan = JSON.stringify(this.tempParam);

      axios
        .put(
          this.$store.getters.getAPI.url +
            "/iems/rpl-matriks/" +
            this.matriksrpl_id,
          formData
        )
        .then(res => {
          swal
            .fire({
              title: `Success`,
              text: "Form berhasil tersimpan!",
              buttonsStyling: false,
              confirmButtonClass: "btn btn-success",
              icon: "success"
            })
            .then(result => {
              this.insertUKID();
              this.getData();
              this.$router.push("daftar");
            });
        })
        .catch(err => {
          console.log(err);
          swal.fire({
            title: `Warning`,
            text: "Form tidak tersimpan!!!",
            buttonsStyling: false,
            confirmButtonClass: "btn btn-warning",
            icon: "warning"
          });
        });
    },
    nullHandler(n) {
      if (n < 1 || n == undefined) {
        return 1;
      }
      return Number(n);
    },
    showDetail() {
      this.$router.push("detail");
    },
    showForm() {
      this.getILID(this.$store.getters.getUKId);
      this.$router.push("form");
    },
    hideForm() {
      this.selects.unitId = "";
      this.periode = "";
      this.$router.push("daftar");
      if (this.$store.getters.isAdmin) {
        this.uk_id = null;
        this.getData(9999);
      }
    }
  }
};
</script>
